
   import React from 'react';
   import Navigation from '../Navigation/Navigation';
   import MobileNav from '../MobileNav/MobileNav';
   
   function Header(props) {

   return (
       <header className={props.headerLarge ? 'header header-large' : 'header' }>
        <h1 className="header__title">&lt;DesireeJoy /&gt;</h1>
         <nav className="nav">
          import {'{'}

          <ul className="nav__list">
         {props.isMobile ? 
                   (<MobileNav theme={props.theme} isMobileNavOpen={props.isMobileNavOpen} handleMobileClose={props.handleMobileClose}/>)
                    :
        ( <Navigation theme={props.theme}/> ) 
        }
        {props.isMobile && (
                    <button
                        className={`header__hamburger header__hamburger_${props.isMobileNavOpen ? 'hidden' : 'null'}`}
                        onClick={props.handleMobileClick}
                    ></button>
                       )}
       </ul>
                       {'}'} from './portfolio'
        </nav>     
                     
                        
      </header>
  )};
  export default Header;