import './App.css';
import React, { useState, useCallback, useEffect, useRef } from 'react';
import {
  HashRouter as Router,
  Routes,
  Route,
} from "react-router-dom";
import Header from '../Header/Header';
import Main from '../Main/Main';
import Contact from '../Contact/Contact';
import Footer from '../Footer/Footer';
import Projectlist from '../Project-list/Project-list';

function App() {
    const [theme, setTheme] = useState('light');
    const [isMobile, setIsMobile] = useState('');
    const [isMobileNavOpen, setMobileNavOpen] = useState(false);
    const [nav, setNav] = useState(false)
    const [stackCollapse, setStackCollapse] = useState(false)
    const [techCollapse, setTechCollapse] = useState(false)
    const [screenWidth, setScreenWidth] = React.useState(window.innerWidth);
    //Added to have classList not toggle on initial mounting of component
    const isInitialMountS = useRef(true);
    const isInitialMountT = useRef(true);




  React.useEffect(() => {

  const handleScreenSizeChange = () => setScreenWidth(window.innerWidth);

  window.addEventListener("resize", handleScreenSizeChange);

  setIsMobile(screenWidth < 768);
  //Mobile menu closes if resized large
  if (!isMobile){
    handleMobileClose();
  }

    return () => window.removeEventListener("resize", handleScreenSizeChange);
  }, [screenWidth]);



  //ChangeBackground of Header on scroll

  const changeBackground = () => {
    console.log(window.scrollY)
    if (window.scrollY <= 400)  {
      setNav(true)
    } else {
      setNav(false)
    }
  }

function handleMobileClick() {
    setMobileNavOpen(true);
  }

function handleMobileClose() {
    setMobileNavOpen(false);
    }


//Checks if the dropdown info/alt-nav should be displayed on state change
useEffect(() => {
  if (isInitialMountS.current) {
    setNav(true);
     isInitialMountS.current = false;
  } else {
       document.querySelector('.cont-stack').classList.toggle('collapsed');
      
  }
},[stackCollapse], [setNav]);

useEffect(() => {
  if (isInitialMountT.current) {
     isInitialMountT.current = false;
  } else {
       document.querySelector('.cont-tech').classList.toggle('collapsed');
  }
},[techCollapse]);






//Toggle the state variable for collapsed section

function toggleState(stateVar){
  if (stateVar === "stack"){
    setStackCollapse(!stackCollapse)
  }
  if (stateVar === "tech"){
    setTechCollapse(!techCollapse)
  }
}
// Toggle the triangle rotation for collapsing section

function rotateTriangle(stateVar){
  const triangle = document.querySelector(stateVar);
  triangle.classList.toggle('object__triangle-rotate');
}

//What to do if they click the triangle for stack
  function handleStackTriangleClick(e){
          e.preventDefault();
          setStackCollapse(true);
          toggleState("stack")
          rotateTriangle('.tri-stack');
    }

  function handleTechTriangleClick(e){
          e.preventDefault();
          setTechCollapse(true);
          toggleState("tech")
          rotateTriangle('.tri-tech');
    }
function handleLightClick(e){
       e.preventDefault();
       setTheme('light')
       
     }
  function handleDarkClick(e){
       e.preventDefault();
       setTheme('dark')
     }
  return (
<>

<div className={`page ${theme === 'dark' ? 'page-dark' : ''}`}>
      <Header theme={theme} isMobile={isMobile} nav={nav} isMobileNavOpen={isMobileNavOpen} handleMobileClose={handleMobileClose} handleMobileClick={handleMobileClick} />
      <div className='header__theme-cont'>
      { theme === "light" ? <input type='button' className='header__theme-btn sun-black disable' disabled />: <input type='button' className='header__theme-btn sun-white' onClick={handleLightClick} />}
      { theme === "light" ? <input type='button' className='header__theme-btn moon-black' onClick={handleDarkClick}/>: <input type='button' className='header__theme-btn moon-white disable' disabled />}
 </div>
     <div className='object'> 
<Routes>


    <Route path='/projects' element={<Projectlist theme={theme}/>}/>
        <Route path='/Contact' element={<Contact theme={theme} />}/>
    <Route exact path="/" element={<Main theme={theme} handleStackTriangleClick={handleStackTriangleClick} handleTechTriangleClick={handleTechTriangleClick}/>} />



</Routes>
      
</div>
<Footer />
             </div>
             </>

    );
  }


export default App;
